.missao-overlay {
  position: absolute;
  width: 100%;
  top: 58px;
  display: none; }

.missao-overlay.active {
  display: flex; }

.modal-missao {
  width: 100%; }
  .modal-missao .modal-missao__close {
    display: none; }
  .modal-missao .modal-missao__header {
    padding-top: 21px;
    padding-bottom: 66px;
    background: #0984E3; }
    .modal-missao .modal-missao__header .modal-missao__header-image {
      display: flex;
      justify-content: center; }
  .modal-missao .modal-missao__body {
    margin-top: -46px;
    background: #DFE6E9;
    border-radius: 40px 40px 0 0; }
    .modal-missao .modal-missao__body .modal-missao__body-container {
      width: 250px;
      margin: 0 auto; }
      .modal-missao .modal-missao__body .modal-missao__body-container .missao__body-title {
        text-align: center;
        padding-top: 20px; }
        .modal-missao .modal-missao__body .modal-missao__body-container .missao__body-title h2 {
          font-size: 22px;
          font-family: Montserrat, sans-serif;
          text-transform: uppercase; }
      .modal-missao .modal-missao__body .modal-missao__body-container .missao__body-points {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 16px 0; }
        .modal-missao .modal-missao__body .modal-missao__body-container .missao__body-points .missao__body-points-icone {
          background-image: url(../img/icon-star.svg);
          display: block;
          width: 43px;
          height: 45px;
          background-repeat: no-repeat;
          background-size: contain;
          padding-bottom: 50px; }
        .modal-missao .modal-missao__body .modal-missao__body-container .missao__body-points .missao__body-points-text {
          font-family: Montserrat, sans-serif;
          text-transform: uppercase;
          font-size: 18px; }
      .modal-missao .modal-missao__body .modal-missao__body-container .missao__body-descricao {
        margin-bottom: 10px; }
        .modal-missao .modal-missao__body .modal-missao__body-container .missao__body-descricao article {
          text-align: center;
          font-size: 19px;
          font-family: Montserrat, sans-serif; }
  .modal-missao .modal-missao__footer {
    background: #DFE6E9;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -10px;
    padding: 40px 0; }

@media only screen and (min-width: 768px) {
  .missao-overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: rgba(45, 52, 54, 0.5);
    display: none;
    justify-content: center;
    align-items: center; }
  .modal-missao {
    width: 672px;
    border-radius: 20px;
    margin-top: 46px;
    position: relative; }
    .modal-missao .modal-missao__close {
      display: block;
      position: absolute;
      right: 0;
      top: 0;
      background: transparent;
      border: 0;
      width: 69px;
      height: 69px;
      cursor: pointer; }
    .modal-missao .modal-missao__header {
      border-radius: 33px 33px 0 0;
      padding-top: 60px;
      padding-bottom: 86px; }
    .modal-missao .modal-missao__body {
      border-radius: 0 0 0 0;
      padding-top: 15px; }
    .modal-missao .modal-missao__footer {
      border-radius: 0 0 33px 33px; } }

.card-missao {
  width: 280px;
  box-shadow: 0px 4px 4px #2D3436;
  border: 0.5px solid #636E72;
  border-radius: 20px;
  display: flex;
  margin-bottom: 20px;
  cursor: pointer; }
  .card-missao .card-missao__imagem {
    height: 100%;
    border-radius: 20px 0 0 20px;
    padding: 10px;
    background: tomato;
    width: 80px;
    display: flex;
    align-items: center;
    justify-content: center; }
    .card-missao .card-missao__imagem img {
      max-width: 100%;
      height: auto; }
  .card-missao .card-missao__body {
    padding: 20px 20px 20px 10px;
    font-family: Montserrat, sans-serif; }
    .card-missao .card-missao__body .card-missao__titulo {
      padding-bottom: 10px; }
      .card-missao .card-missao__body .card-missao__titulo h3 {
        font-size: 16px;
        color: #2D3436; }
    .card-missao .card-missao__body .card-missao__pontos {
      text-transform: uppercase;
      font-size: 12px;
      display: flex;
      align-items: center; }
      .card-missao .card-missao__body .card-missao__pontos .icon {
        margin-right: 10px; }

@media only screen and (min-width: 768px) {
  .card-missao {
    margin-bottom: 48px; } }

